function sliderServices() {
    var swiper = new Swiper('.services .swiper', {
        slidesPerView: '2',
        spaceBetween: 230,
        loop: false,
        navigation: {
            nextEl: '.services .swiper-button-next',
            prevEl: '.services .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            768: {
                slidesPerView: '2',
            },
            992: {
                spaceBetween: 50,
            },
            1200: {
                slidesPerView: '2',
                spaceBetween: 230,
            },
        }
    })
}

$(document).ready(function() {

    sliderServices();

  $('.header-top__burger').click(function () {
      $(this).toggleClass('header-top__burger--active')
      $('.header-bottom__list').toggleClass('header-bottom__list--active')
      $('body').toggleClass('fixed-body')
  })

    // Tabs
    $(".services__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".services__tab").removeClass("services__tab--active")
        $(this).addClass("services__tab--active")

        $(this).parents(".services__tabs").find(".services__content").removeClass("services__content--active")
        $(this).parents(".services__tabs").find(`.services__content[data-content-path="${path}"]`).addClass("services__content--active")
    })

})
